/* Page Wrapper */
.pageWrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0px;
}

/* Banner Section */
.banner {
  position: relative;
  width: 100%;
  height: 500px;
  background-image: url('/public/images/banner.jpg'); /* Ensure this file exists */
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  margin: 0;
  padding: 0px;

}

.bannerContent {
  position: relative;
  z-index: 2;
  max-width: 80%;
}

.bannerText, .bannerSubText {
  font-size: 30px;
  font-weight: bold;
  background: linear-gradient(to right, rgba(77, 77, 77, 0.6), rgba(77, 77, 77, 0.3)); /* Smooth fade effect */
  padding: 3px 10px;
  border-radius: 5px;
  color: white;
  backdrop-filter: blur(2px); /* Adds a modern blur effect */
}
  
.bannerSubText {
  font-size: 14px;
  margin-top: 0px;
  font-weight: normal;

}


/* CTA Button */
.ctaButton {
  display: inline-block;
  margin-top: 20px;
  padding: 15px 30px;
  font-size: 18px;
  color: white;
  background: #ff9800;
  border-radius: 5px;
  text-decoration: none;
  transition: background 0.3s ease, transform 0.2s;
}

.ctaButton:hover {
  background: #e68900;
  transform: scale(1.05);
}

/* Why Choose Us Section */
.whyChoose {
  text-align: center;
  margin-top: 50px;
}

.whyChoose h2 {
  font-size: 28px;
  color: #004d99;
  margin-bottom: 20px;
}

.whyGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  justify-content: center;
  margin-top: 20px;
}

.feature {
  background: #f5f5f5;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  transition: transform 0.3s ease;
}

.feature:hover {
  transform: translateY(-5px);
}

.icon {
  font-size: 40px;
  color: #ff9800;
  margin-bottom: 10px;
}

/* Mobile Optimization */
@media (max-width: 768px) {
  .banner {
    height: 350px;
    padding: 10px;
  }

  .bannerText {
    font-size: 28px;
  }

  .bannerSubText {
    font-size: 16px;
  }

  .ctaButton {
    padding: 12px 24px;
    font-size: 16px;
  }

  .whyGrid {
    grid-template-columns: 1fr;
  }
}
