/* Container */
.container {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  text-align: center;
}

/* Form */
.form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.input, .textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  transition: border-color 0.3s;
}

.input:focus, .textarea:focus {
  border-color: #004d99;
  outline: none;
}

/* Button */
.button {
  background: #ff9800;
  color: white;
  padding: 12px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.button:hover {
  background: #e68900;
}

/* Feedback Messages */
.feedback {
  margin-top: 10px;
  padding: 10px;
  font-size: 14px;
  border-radius: 5px;
}

.success {
  background: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.error {
  background: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

/* Mobile Optimization */
@media (max-width: 768px) {
  .container {
    padding: 15px;
  }

  .button {
    font-size: 14px;
    padding: 10px;
  }
}